import React, { Component } from "react";
import Newsitems from "./Newsitems";
import Loading from "./Loading";
import InfiniteScroll from "react-infinite-scroll-component";

export default class News extends Component {
  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      total: 0,
      loading: false,
      page: 1,
    };
    document.title = `${this.capitalize(this.props.category)} - ReactNews`;
  }

  capitalize(string) {
    return string[0].toUpperCase() + string.slice(1).toLowerCase();
  }

  // From .env.local file
  apiKey = process.env.REACT_APP_NEWSAPI;

  // This will run after render()
  async componentDidMount() {
    this.props.setProgress(40);
    this.setState({
      loading: true,
    });
    let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=${this.apiKey}&page=${this.state.page}&pageSize=12`;
    let data = await fetch(url);
    this.props.setProgress(65);

    let parsedData = await data.json();
    this.props.setProgress(80);

    this.setState({
      articles: parsedData.articles,
      total: parsedData.totalResults,
      loading: false,
      page: this.state.page + 1,
    });
    this.props.setProgress(100);
    this.props.setProgress(0);
  }

  // Handle Both nextPage() and prevPage()
  changePage = async (page) => {
    this.setState({
      loading: true,
    });

    let [url, newPage] =
      page === "previous"
        ? [
            `https://newsapi.org/v2/top-headlines?country=in&category=${
              this.props.category
            }&apiKey=f95bbc4561f74b568b0882dd6f02be0b&page=${
              this.state.page - 1
            }&pageSize=12`,
            this.state.page - 1,
          ]
        : [
            `https://newsapi.org/v2/top-headlines?country=in&category=${
              this.props.category
            }&apiKey=f95bbc4561f74b568b0882dd6f02be0b&page=${
              this.state.page + 1
            }&pageSize=12`,
            this.state.page + 1,
          ];

    let data = await fetch(url);
    let parsedData = await data.json();

    this.setState({
      articles: parsedData.articles,
      loading: false,
      page: newPage,
    });
  };

  fetchMoreData = async () => {
    let url = `https://newsapi.org/v2/top-headlines?country=in&category=${this.props.category}&apiKey=f95bbc4561f74b568b0882dd6f02be0b&page=${this.state.page}&pageSize=12`;
    let data = await fetch(url);
    let parsedData = await data.json();

    this.setState({
      // Old data + New data
      articles: this.state.articles.concat(parsedData.articles),
      page: this.state.page + 1,
    });
  };

  render() {
    return (
      <div>
        <h1 className="my-4 text-center">
          Top News - {this.capitalize(this.props.category)}
        </h1>

        {this.state.loading ? (
          <div className="text-center mb-4">
            <Loading />
          </div>
        ) : (
          <InfiniteScroll
            dataLength={this.state.articles.length}
            next={this.fetchMoreData}
            hasMore={this.state.articles.length !== this.state.total}
            loader={
              <div className="text-center my-4">
                <Loading />
              </div>
            }
            scrollableTarget="scrollableDiv"
          >
            <div>
              <div className="container my-4 d-flex flex-wrap justify-content-around">
                {this.state.articles.map((element) => {
                  return (
                    <Newsitems
                      key={element.url}
                      title={element.title ? element.title.substr(0, 45) : ""}
                      desc={
                        element.description
                          ? element.description.substr(0, 120)
                          : ""
                      }
                      img={
                        element.urlToImage
                          ? element.urlToImage
                          : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQdNPBgD4MrqX1d3PmjdFv60qpBZ8L13sd2Mg&usqp=CAU"
                      }
                      date={new Date(element.publishedAt).toGMTString()}
                      more={element.url ? element.url : ""}
                    />
                  );
                })}
              </div>

              {/* Next & Prev Buttons */}
              {/* <div className="container mb-4 d-flex justify-content-around">
              <button
                // Disabled when on first page
                disabled={this.state.page <= 1}
                type="button"
                className="btn btn-dark"
                onClick={() => {
                  this.changePage("previous");
                }}
              >
                &larr;Prevoius
              </button>
              <button
                // Disabled on last page
                disabled={this.state.articles.length < 12 ? true : false}
                type="button"
                className="btn btn-dark"
                onClick={() => {
                  this.changePage("next");
                }}
              >
                Next&rarr;
              </button>
            </div> */}
            </div>
          </InfiniteScroll>
        )}
      </div>
    );
  }
}
