import React, { Component } from "react";

export default class Newsitems extends Component {
  render() {
    // We can also use the props like bellow
    // let { title, desc, imgUrl, more } = this.props;
    return (
      <div>
        <div className="card my-4" style={{ width: "20rem" }}>
          <img
            className="card-img-top"
            style={{ height: "180px" }}
            src={this.props.img}
            alt="Card image cap"
          />
          <div className="card-body">
            <h5
              className="card-title text-center
            "
            >
              {this.props.title}...
            </h5>
            <p className="card-text">{this.props.desc}...</p>
            <span className="badge badge-secondary">{this.props.date}</span>
            <a href={this.props.more} target="_blank" className="float-right">
              More...
            </a>
          </div>
        </div>
      </div>
    );
  }
}
