import "./App.css";
import React, { Component } from "react";
import Navbar from "./components/Navbar";
import News from "./components/News";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import LoadingBar from "react-top-loading-bar";

export default class App extends Component {
  state = {
    progress: 0,
  };

  setProgress = (progressVal) => {
    this.setState({
      progress: progressVal,
    });
  };

  render() {
    return (
      <div>
        <Router>
          <LoadingBar
            color="#f11946"
            progress={this.state.progress}
            height={4}
          />
          <Navbar />
          <Switch>
            <Route key="home" exact path="/">
              <News setProgress={this.setProgress} category="general" />
            </Route>
            <Route key="ent" exact path="/entertainment">
              <News setProgress={this.setProgress} category="entertainment" />
            </Route>
            <Route key="hlth" exact path="/health">
              <News setProgress={this.setProgress} category="health" />
            </Route>
            <Route key="sci" exact path="/science">
              <News setProgress={this.setProgress} category="science" />
            </Route>
            <Route key="sprt" exact path="/sports">
              <News setProgress={this.setProgress} category="sports" />
            </Route>
            <Route key="tech" exact path="/technology">
              <News setProgress={this.setProgress} category="technology" />
            </Route>
          </Switch>
        </Router>
      </div>
    );
  }
}
